:root {
  --default-radius: 10px;
  --default-padding: 5px;
  --default-buttonhover-color: rgb(240, 240, 240);
  --default-button-hover-font-color: rgb(172, 172, 172);
}

.App {
  /* background-color: rgba(245, 245, 245, 0.326); */

  text-align: center;
}

.switch__chainnetwork {
  position: fixed;
  z-index: 9999;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  overflow-y: none;
  width: 100%;
  background: rgb(83, 28, 254);
  background: linear-gradient(
    166deg,
    rgba(83, 28, 254, 1) 0%,
    rgba(97, 203, 242, 1) 86%
  );
  /* background: transparent; */
  opacity: 0.95;
  color: white;
}

.switch__chainnetwork > h1 {
  margin: 0 50px;
}

.switch__chainnetwork > p {
  margin: 0 20px;
}

.switch__chainnetwork > button {
  padding: 15px;
  background-color: black;
  color: #ffffc4;
  border: 3px solid #484848;
  border-radius: 10px;
  margin-top: 10px;
  cursor: pointer;
}
