.create-post {
  background-color: rgb(255, 255, 255);
  grid-area: createpost;
  border-radius: var(--default-radius);
  padding: 0 14px 0 14px;

  display: grid;
  grid-template-columns: 0.08fr 1fr;
  grid-template-rows: 1fr 0.1fr;
  grid-template-areas:
    "pic article"
    "picw button";

  max-width: 100%;
  grid-gap: 20px 10px;
}

.user-profile-image {
  width: 100%;
  grid-area: pic;
}

.create-post-input {
  grid-area: article;
}

.create__posttextinput {
  border-radius: var(--default-radius);
  width: 98%;
  padding-left: 9px;
  padding-top: 10px;
  align-self: center;
  height: 4.6em;
  border: 0mm;
  outline: none;
  backdrop-filter: blur(190px) saturate(360%);
  resize: none;
  border-bottom: 1px solid whitesmoke;
}

.create__posttextinput:focus {
  height: 5.6em;
  font-size: 15px;
}

.create__posttextinput::placeholder {
  color: rgb(160, 160, 160);
  font-size: 15px;

  padding: 0 0 0 10px;
}

.button__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.button__list > button {
  margin-right: 14px;
}

.graded__button {
  color: lightgray !important;
  background-color: #f1f1f1 !important;
}

