.button__list button,
.post__submitbutton {
  background-color: #fff;
  border: 1px solid rgb(231, 231, 231);
  cursor: pointer;
  color: rgb(58, 58, 58);
  padding: 10px 10px;
  border-radius: var(--default-radius);
  font-size: 13px;

  display: flex;
  justify-content: space-between;
}

.create__postbuttons {
  grid-area: button;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: -5px;
}

.button__list > button:hover,
.post__submitbutton:hover {
  background-color: var(--default-buttonhover-color);
  color: --default-button-hover-font-color;
}

@media (min-width: 320px) and (max-width: 768px) {
  .create-post-input input::placeholder {
    font-size: 1em;
  }

  .button__list button,
  .post__submitbutton {
    padding: 5px 5px;
    font-size: 11px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 45px;
    min-height: 40px;
    max-height: 40px;
  }

  .user-profile-image {
    width: 40px !important;
    height: 40px !important;
    grid-area: pic;
  }

  .create__postbuttons {
    align-items: center;
  }
}
