.login__success {
  background-color: white;
  padding: 10px;
  position: absolute;
  top: 30px;
  right: 10px;
  z-index: 999;
  border: 2px solid whitesmoke;

  font-size: 14px;
  font-weight: 700;
  width: 190px;

  text-align: center;
  color: #353535;

  display: flex;
  justify-content: space-evenly;
  align-items: center;

  animation: fadeInAnimation ease 1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  color: limegreen;
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
