.side-bar {
  top: 115px;
  position: sticky;
  height: fit-content;
  grid-area: sidebar;
  border-radius: var(--default-radius);
  flex: 0.21;
}

@media (min-width: 320px) and (max-width: 768px) {
  .side-bar {
    display: none !important;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .side-bar {
    flex: 0.3;
  }
}
