.nav-bar {
  top: 0;
  position: sticky;
  z-index: 999;
}

.notice__header {
  background: rgb(83, 28, 254);
  background: linear-gradient(
    166deg,
    rgba(83, 28, 254, 1) 0%,
    rgba(97, 203, 242, 1) 86%
  );
  color: white;
  padding: 5px 0;
  font-size: 14px;
}

.nav-bar > nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ffffff;
  border-bottom: 1px solid whitesmoke;
  padding: 3px 200px 3px 200px;
}

.lo-image-logo {
  object-fit: contain;
  height: 35px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.navbar__left {
  color: #51aff7;
  font-weight: 800;
  font-size: 25px;
  flex: 0.2;
  text-align: left;
}

.navbar__right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0.26;
}

.navbar__center {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 0.4;
  margin-left: -50px;
}

.login-button {
  margin-right: 10px;
  /* border: 1px solid lightgray; */
  /* border: 1px solid #0b8df0; */
  background-color: #242424;
  /* background: rgb(0, 212, 255);
  background: linear-gradient(
    90deg,
    rgba(0, 212, 255, 1) 0%,
    rgba(255, 146, 0, 1) 91%,
    rgba(255, 191, 0, 1) 100%
  ); */
  padding: 5px 15px;
  border-radius: var(--default-radius);
  cursor: pointer;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  border: none;
}

.login-button > p {
  margin-right: 5px;
  font-size: 14px;
}

.signup-button {
  background-color: #242424;
  /* background: rgb(0, 212, 255);
  background: linear-gradient(
    90deg,
    rgba(0, 212, 255, 1) 0%,
    rgba(255, 146, 0, 1) 91%,
    rgba(255, 191, 0, 1) 100%
  ); */

  padding: 9px 18px;
  border-radius: var(--default-radius);
  cursor: pointer;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: white;
  border: none;
}

.signup-button > p {
  margin-right: 5px;
  font-size: 14px;
}

.login-button:hover,
.signup-button:hover {
  border: none;
  background-color: #585858;
  border: none;
  color: #e6e6e6;
}

.navbar__center > form {
  width: 95%;
  background-color: whitesmoke;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 10px;
  border-radius: 5px;
}

.header__input {
  padding: 10px 10px;
  outline: none;
  width: 80%;
  border: none;
  background-color: transparent;
  font-size: medium;
  color: gray;
}

.header__input::placeholder {
  /* color: rgb(184, 68, 68); */
  color: #b0b0b0;
  font-size: 14px;
}

.search__icon {
  border: none;
  background-color: transparent;
  color: gray;
  cursor: pointer;
}

.user-account-icon {
  box-shadow: inset gray;
}

.theme-icons {
  color: #000000 !important;
  border: 4px solid var(--default-buttonhover-color);
  border-radius: 25px;
  background-color: var(--default-buttonhover-color);
}

.user__address {
  font-size: 13px;
  padding: 5px;
  font-weight: 500;
  cursor: pointer;
}

@media (min-width: 320px) and (max-width: 768px) {
  nav form {
    display: none !important;
  }

  .login-button {
    display: none !important;
  }

  .user-account-icon {
    font-size: 40px;
    color: rgb(187, 187, 187);
    align-self: center;
  }

  .theme-icons {
    font-size: large;
    color: rgb(36, 36, 36);
  }

  .logo-n-searchbar,
  .user__address,
  .theme-icons,
  .header__input {
    display: none !important;
  }

  .lo-image-logo {
    width: 100px;
  }

  .nav-bar > nav {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 2px 15px;
    margin-bottom: 10px;
  }

  .user-account-icon {
    height: 30px !important;
    width: 30px !important;
  }

  .signup-button {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .nav-bar > nav {
    padding: 3px 100px 3px 100px;
  }
  .signup-button {
    width: 85px;
  }
}
