.mobile__navwrapper {
  display: none;
}
/* @media (min-width: 768px) {
} */

@media (min-width: 320px) and (max-width: 768px) {
  .mobile__navwrapper {
    background-color: white;
    border-top: 1px solid whitesmoke;

    position: fixed;
    /* overflow: hidden; */
    width: 100%;
    bottom: 0;
    z-index: 5;
    display: flex;
    justify-content: space-evenly;
    padding: 5px 0;
    color: #505050;
  }

  .bottomB__icons {
    width: 28px !important;
    height: 28px !important;
    cursor: pointer;
  }
}
