.signup__mainwrapper {
  border: none;
  background-color: transparent;
}

.signup__wrapper {
  background-color: rgba(0, 0, 0, 0.694);
  display: flex;
  padding: 100px 230px;
  justify-content: center;

  animation: fadeInAnimation ease 1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.signup__ui {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  border: none;
  background-color: transparent;
  outline: none;
}

.signup__left {
  background-color: #e5eafe;
  min-height: 80vh;
  flex: 0.35;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top-left-radius: var(--default-radius);
  border-bottom-left-radius: var(--default-radius);
}

.signup__left > img {
  object-fit: contain;
  height: 230px;
}

.signup__right {
  background-color: white;
  flex: 0.65;
  display: flex;
  flex-direction: column;
  border-top-right-radius: var(--default-radius);
  border-bottom-right-radius: var(--default-radius);
}

.signupRight__Body {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.signupRight__Body > form {
  display: flex;
  flex-direction: column;

  margin: 10px 40px 10px 40px;
}

.signupRight__Body > form > input {
  margin-top: 10px;
  border-radius: 25px;
  border: none;
  background-color: rgba(211, 211, 211, 0.648);
  padding: 10px 25px;
  outline-color: #2f3436;
}

.signupRight__Body > form > button {
  margin-top: 10px;
  border-radius: 25px;
  cursor: pointer;
  background-color: black;
  color: white;
  padding: 10px 25px;
}

/* .signup__right > form > input::placeholder {
  color: gray;
} */

.modal__close {
  color: gray;
  font-weight: 600;
  cursor: pointer;
}

.signupRight__header {
  display: flex;
  justify-content: flex-end;
  padding: 5px 5px 0 0;
}
.login__section {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.login__section > h5 {
  color: gray;
  margin-bottom: 5px;
}

.login__section > h3 {
  margin: 10px;
  text-align: center;
}

.login__section > p {
  font-size: small;
  margin-top: 5px;
  color: gray;
  margin: 0 20px 0 20px;
  text-align: center;
}

.wallet__connect > img {
  object-fit: contain;
  height: 55px;
  cursor: pointer;
  margin-top: 5px;
}

.load__spinBlack {
  object-fit: contain;
  height: 25px;
  width: 25px;
  margin-right: 5px;
  margin-top: 10px;
}

.load__dots {
  object-fit: contain;
  height: 25px;
  width: 25px;

  margin-left: 10px;
}

.load__gear {
  object-fit: contain;
  height: 120px;
  width: 120px;
  margin-right: 5px;
  margin-top: 10px;
}

.loggingIn__Active {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

@media (min-width: 320px) and (max-width: 1024px) {
  .signup__left {
    display: none;
  }

  .login__section > h3 {
    margin: 10px 20px;
    text-align: center;
  }

  .signupRight__Body {
    padding: 20px 0;
  }

  .signup__wrapper {
    padding: 0;
  }

  .signup__right {
    border-top-left-radius: var(--default-radius);
    border-bottom-left-radius: var(--default-radius);
    flex: 0.95;
  }

  .wallet__connect {
    margin-bottom: 20px;
  }
}
