.feed {
  grid-area: feed;
  flex: 0.55;
  margin: 0 1.5em 0 1.5em;
  border-left: 2px solid whitesmoke;
  border-right: 2px solid whitesmoke;
  margin-top: 5px;
}

.newuserlogin__warning {
  border-bottom: 1px solid whitesmoke;
  padding: 0 10px 10px 10px;

  position: sticky;
  top: 1;

  z-index: 998;
  /* margin-top: -30px; */
}

.newuserlogin__warning > div {
  padding: 7px 15px;
  border-radius: 25px;
  border-width: 1px;
  border-style: solid;
  border-color: lightgrey;
  /* border-color: rgb(83, 28, 254); */
  /* border-color: linear-gradient(
    166deg,
    rgba(83, 28, 254, 1) 0%,
    rgba(97, 203, 242, 1) 86%
  ); */
  /* background-color: gray; */
  display: flex;
  justify-content: center;
  align-items: center;
  color: gray;
  font-size: 12px !important;
}

.newuserlogin__warning > div:hover {
  cursor: no-drop;
  border-color: whitesmoke;
}

.newuserlogin__warning p {
  margin-left: 5px;
}

.hr_rule {
  border-top: 1px solid #ededed;
  margin: 20px 0 20px 0;
  width: 100%;
  margin-left: 5px;
}

.post_partitionInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px 0 15px;
}

.post_partitionInfo > p {
  font-size: 12px;
  color: #aeaeae;
}

@media (min-width: 320px) and (max-width: 768px) {
  .feed {
    margin: 0;
    /* margin-right: -75px; */
  }

  .newuserlogin__warning {
    padding: 0;
    margin-top: -18px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .feed {
    flex: 0.7;
  }
}
