.post {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.1fr 0.5fr 0.1fr;
  grid-template-areas:
    "userPicAndDate"
    "articlePost"
    "icons";
  grid-row-gap: 0.4em;
  border-bottom: 1px solid #f1f1f1;
  background-color: rgb(255, 255, 255);
  padding: 10px 15px 10px 15px;
  border-radius: var(--default-radius);
  margin: 0 0 8px 0;
}

.userPicAndDate {
  grid-area: userPicAndDate;
  display: grid;
  grid-template-columns: 0.67fr 2fr 6fr;
  grid-template-rows: 1fr;
  grid-template-areas: "user-pic user-name postmore";
  padding: var(--default-padding);
  grid-column-gap: 0.5em;
}

.pic-div {
  grid-area: user-pic;
}

.user-name {
  grid-area: user-name;
  align-self: center;
  color: #393939;
  text-align: left;
}

.user-name h5 {
  font-weight: 700;
  text-transform: capitalize;
}

.user-name p {
  font-size: 13px;
  color: var(--default-button-hover-font-color);
  font-weight: 400;
}

.post-more-option {
  grid-area: postmore;
  display: flex;
  justify-content: flex-end;

  color: gray;
}

.profile-pic {
  width: 100%;
  margin-left: -5px;
}

.article-post {
  grid-area: articlePost;
  margin-right: 25px;
  /* margin-left: 10px; */
  white-space: pre-line;
}

.article-post article {
  text-align: left;
  margin-top: -15px;
  font-size: 15px;
  margin-left: 55px;
}

.post-icons > div {
  grid-area: icons;
  display: flex;
  justify-content: space-evenly;

  /* padding-top: 5px;
  padding-right: 10px; */

  margin-top: 15px;
  color: gray;
  margin-left: -50px;

  /* border-top: 1px solid whitesmoke; */
}

.interact-icons {
  margin-left: 15px;
  width: 20px !important;
  cursor: pointer;
}

@media (min-width: 320px) and (max-width: 768px) {
  .user-name {
    font-size: 16px;
  }

  .userPicAndDate {
    display: grid;
    grid-template-columns: 0.9fr 7fr 3fr;
  }

  .post-icons {
    margin-top: 14px;
    margin-left: 20px;
  }

  .interact-icons {
    margin-left: 11px;
    font-size: 15px;
  }

  .profile-pic {
    width: 40px !important;
    height: 40px !important;
    margin-left: -5px;
  }

  .article-post article {
    text-align: left;
    margin-top: -7px;
    font-size: 14px;
    margin-left: 50px;
  }
}
